<template>
  <div class="loginMain">
    <Header></Header>
    <router-view></router-view>
  </div>
</template>

<script>
import Header from 'components/common/Header';
export default {
  name: 'Login',
  components: {
    Header
  },
}
</script>

<style>
.loginMain {
  width: 100vw;
  height: 100vh;
  background-image: url("~assets/img/bg1.jpg");
  /* background: -webkit-linear-gradient(45deg, skyblue, pink); */
  background-size: cover;
  background-position: 0px, -10px;
  overflow: hidden;
}

.loginMain .title {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
  cursor: default;
}

.loginMain input {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  box-sizing: border-box;
  font-size: 16px;
  color: #fff;
  background-color: rgba(80, 80, 80, 0.3);
  transition: box-shadow 0.3s;
}

.loginMain input:focus {
  box-shadow: 0 2px 3px 0 rgb(0 0 0 / 10%) inset, 0 2px 7px 0 rgb(0 0 0 / 20%);
}

.loginMain button {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  font-size: 16px;
  color: #eee;
  background-color: #ff8656;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.loginMain button:hover {
  background-color: #f05418;
}

.loginMain a {
  display: inline-block;
  font-size: 12px;
  cursor: pointer;
  color: #444;
  transition: color 0.3s;
}

.loginMain a:hover {
  color: #000;
}
</style>


